<template>
  <div class="home">
    <el-container>
      <el-header style="width: 1000px;height: 600px">
        <testStarter class="starter" @testStart="saveOneTest" ref="testStarter"></testStarter>
      </el-header>
      <el-main>
        <about class="about" ref="about"></about>
      </el-main>
    </el-container>
  </div>
</template>

<script>
  import TestStarter from './TestStarter'
  import About from '@/components/common/About'

  export default {
    name: 'AppIndex',
    components: {TestStarter, About},
    methods: {
      saveOneTest () {
        this.$router.push('/test')
      }
    }
  }
</script>

<style scoped>
  .starter {
    width: 990px;
    heigth: 2000px;
    margin-left: auto;
    margin-right: auto;
  }

  .home {
    width: 990px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
  }

</style>



