<template>
 <div class="TestStarter">
      <el-card  style="text-align: left;width: 600px;height:550px;margin: 0px 0px 30px auto">
      <h1><b>请设置您需要的测试信息: </b></h1>
      <div>目标系统:
        <select v-model="form.systemid">
          <option disabled value="">Please select one</option>
          <option value="0">Apollo</option>
          <option value="1">Autoware</option>
        </select>
        </div>
      <div>初始场景选取:
        <select v-model="form.initialscenario.id">
          <option disabled value="">Please select one</option>
          <option value="0">从场景库选取</option>
          <option value="1">本地上传</option>
      </select>
      </div>
      <div>场景测试时长:
      <input v-model="form.duration" placeholder="Please enter a number" /><span>小时</span>
      </div>
      <br />
      <br />
      <div>场景变异对象: </div>
      <span>环境要素</span>
        <input type="checkbox" id="100" value="100" v-model="Aenvironments" />
        <label for="天气">天气</label>
        <input type="checkbox" id="010" value="10" v-model="Aenvironments" />
        <label for="时间">时间</label>
        <input type="checkbox" id="001" value="1" v-model="Aenvironments" />
        <label for="道路表面">道路表面</label>
      <br />
      <span>静态障碍物</span>
        <input type="checkbox" id="100" value="100" v-model="Astatic_obstacles" />
        <label for="行人">行人</label>
        <input type="checkbox" id="010" value="10" v-model="Astatic_obstacles" />
        <label for="自行车">自行车</label>
        <input type="checkbox" id="001" value="1" v-model="Astatic_obstacles" />
        <label for="路障">路障</label>
      <br />
      <span>动态障碍物</span>
        <input type="checkbox" id="10" value="10" v-model="Adynamic_obstacles" />
        <label for="卡车">卡车</label>
        <input type="checkbox" id="01" value="1" v-model="Adynamic_obstacles" />
        <label for="出租车">出租车</label>
      <br />
      <span>交通设施</span>
        <input type="checkbox" id="10" value="10" v-model="Atraffic_facilities" />
        <label for="交通信号灯">交通信号灯</label>
        <input type="checkbox" id="01" value="1" v-model="Atraffic_facilities" />
        <label for="交通标志">交通标志</label>
      <br />
      <br />
      <div>目标事故类型: </div>
        <input type="checkbox" id="10" value="10" v-model="Atarget_accidents" />
        <label for="碰撞">碰撞</label>
        <input type="checkbox" id="01" value="1" v-model="Atarget_accidents" />
        <label for="驶出道路">驶出道路</label>
      <br />
      <div>场景评估策略:</div>
        <input type="checkbox" id="100" value="100" v-model="Aevaluation_strategies" />
        <label for="场景多样性">场景多样性</label>
        <input type="checkbox" id="010" value="10" v-model="Aevaluation_strategies" />
        <label for="与周围车辆的安全距离">与周围车辆的安全距离</label>
        <input type="checkbox" id="001" value="1" v-model="Aevaluation_strategies" />
        <label for="规划路径的偏移">规划路径的偏移</label>
      <br />
      <br />
      <button @click="handleStart">启动测试</button>
      </el-card>
    </div>
</template>

<script>
    export default {
      name: 'TestStarter',
      data () {
        return {
          Aenvironments: [],
          Astatic_obstacles: [],
          Adynamic_obstacles: [],
          Atraffic_facilities: [],
          Atarget_accidents: [],
          Aevaluation_strategies: [],
          form: {
            duration: '',
            stateid: '',
            systemid: '',

            initialscenario: {
              id: '',
              type: ''
            },
            environments: {
              id: '',
              type: ''
            },
            staticobstacles: {
              id: '',
              type: ''
            },
            dynamicobstacles: {
              id: '',
              type: ''
            },
            trafficfacilities: {
              id: '',
              type: ''
            },
            targetaccidents: {
              id: '',
              type: ''
            },
            evaluationstrategies: {
              id: '',
              type: ''
            }
          }
        }
      },
      methods: {
        handleStart () {
          var date = new Date()
          var date1 = date.toLocaleDateString()
          var s1 = 0
          var s2 = 0
          var s3 = 0
          var s4 = 0
          var s5 = 0
          var s6 = 0
          for (var i = 0; i < this.Aenvironments.length; i++) {
            s1 += Number(this.Aenvironments[i])
          }
          for (var j = 0; j < this.Astatic_obstacles.length; j++) {
            s2 += Number(this.Astatic_obstacles[j])
          }
          for (var k = 0; k < this.Adynamic_obstacles.length; k++) {
            s3 += Number(this.Adynamic_obstacles[k])
          }
          for (var m = 0; m < this.Atraffic_facilities.length; m++) {
            s4 += Number(this.Atraffic_facilities[m])
          }
          for (var n = 0; n < this.Atarget_accidents.length; n++) {
            s5 += Number(this.Atarget_accidents[n])
          }
          for (var g = 0; g < this.Aevaluation_strategies.length; g++) {
            s6 += Number(this.Aevaluation_strategies[g])
          }
          this.form.environments.id = s1
          this.form.staticobstacles.id = s2
          this.form.dynamicobstacles.id = s3
          this.form.trafficfacilities.id = s4
          this.form.targetaccidents.id = s5
          this.form.evaluationstrategies.id = s6
          this.$axios
            .post('/edit/testcases', {
            date: date1,
            duration: this.form.duration,
            stateid: 0,
            systemid: this.form.systemid,
            initialscenario: this.form.initialscenario,
            environments: this.form.environments,
            staticobstacles: this.form.staticobstacles,
            dynamicobstacles: this.form.dynamicobstacles,
            trafficfacilities: this.form.trafficfacilities,
            targetaccidents: this.form.targetaccidents,
            evaluationstrategies: this.form.evaluationstrategies
          }).then(resp => {
            if (resp && resp.data.code === 200) {
            console.log(this.form)
            this.$emit('testStart')
            }
          })
        }
      }
    }
</script>

<style scoped>
  .TestStarter {
    top: 100px;
    width: 700px;
    height: 1000px;
  }
</style>



